body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0 auto;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
  scroll-padding:
    5rem;
  scroll-padding:
    var(--scroll-padding, 5rem);
}

/* This is for the git push */
/* Navbar CSS */
li {
    list-style: none;
    padding: 20px;
    transition: background-color 500ms ease-out;
}

nav div h1 {
    padding: 15px;
    transition: background-color 500ms ease-out;
}

nav div h1:hover {
    cursor: pointer;
    background-color: white;
    color: black;
}

.list--wrapper li:hover {
    cursor: pointer;
    background-color: white;
    color: black;
}

.nav--wrapper {
    position: fixed;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: white;
    justify-content: center;
    transition: background-color 500ms ease-out;
    border-bottom: 1px solid white;
    z-index: 1;
}

.list--wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
}

/* Dark mode toggler */
.dark-mode-container {
    margin-left: 10px;
}

.bullet {
    display: flex;
    margin: auto;
    padding: 5px 0px;
    align-items: center;
    justify-content: flex-start;
    background-color: rgb(46, 46, 46);
    height: 20px;
    width: 40px;
    border-radius: 12px;
}

.circle {
    background-color: white;
    margin: 0;
    height: 20px;
    width: 20px;
    border-radius: 50%;
}

.circle:hover {
    cursor: pointer;
}

.dark.bullet {
    justify-content: flex-end;
    border: 1px solid white;
    box-shadow: white;
    background-color: rgb(28, 28, 28);
}

.dark {
    background-color: rgb(28, 28, 28);
    color: white;
}
.main {
    height: 100vh;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    transition: background-color 500ms ease-out;
  }
  
  .typewritter {
    white-space: nowrap;
    border-right: 3px solid rgb(255, 0, 242);
    overflow: hidden;
    -webkit-animation: 
      typing 3s steps(30, end),
      blink-caret .75s step-end infinite;
            animation: 
      typing 3s steps(30, end),
      blink-caret .75s step-end infinite;
  }
  
  .main-p {
    letter-spacing: 0.6rem;
  }
  
  .dark {
    background-color: rgb(5, 14, 21);
    color: white;
  }
  
  @-webkit-keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  @keyframes typing {
    from { width: 0 }
    to { width: 100% }
  }
  
  @-webkit-keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: rgba(35, 95, 148, 0.65); }
  }
  
  @keyframes blink-caret {
    from, to { border-color: transparent }
    50% { border-color: rgba(35, 95, 148, 0.65); }
  }

  /* Icons */
  .icons {
    max-width: 70%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 10px;
  }

  .icon-pack {
    height: 24px;
    width: 24px;
    transition: -webkit-transform 300ms ease-out;
    transition: transform 300ms ease-out;
    transition: transform 300ms ease-out, -webkit-transform 300ms ease-out;
  }

  .icon-pack:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    cursor: pointer;
  }

 .icon-pack {
    color: black;
  }

  .dark .icon-pack {
    color: white;
  }

  @media (max-width: 480px) {
    .main-container {
      text-align: center;
    }
    .main-container h1 {font-size: 24px;}
    .main-container p {letter-spacing: 5px;}
  }
.window-btn {
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue';
    position: relative;
    font-size: 16px;
    bottom: 0px;
    background: none;
    border: none;
    color: white;
    border-bottom: 1px solid white;
    padding: 10px 20px;
}

.window-btn:hover {
    cursor: pointer;
    -webkit-animation: bouncing 1.1s ease-out infinite;
            animation: bouncing 1.1s ease-out infinite
}

.dark.window-btn {
    color: black;
    border-bottom: 1px solid black;
}

@-webkit-keyframes bouncing {
    0% {bottom: 0px}
    50% {bottom: 5px}
}

@keyframes bouncing {
    0% {bottom: 0px}
    50% {bottom: 5px}
}
.section-container {
    height: 100vh;
    transition: background-color 500ms ease-out;
}

.content-container {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    /* max-width: 50%; */
    height: 100%;
    grid-gap: 10px;
    gap: 10px;
}

.info-container {
    text-align: center;
    display: flex;
    flex-direction: column;
    max-width: 500px;
}

.skill-list-info {
    font-style: italic;
    font-weight: 700;
}

@media (max-width: 480px) {
    .info-container {
        width: 50%;
    }
}
.item-container {
    height: 200px;
    width: 350px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin: 100px;
}

.code-container {
    padding: 10px;
}

.live {
    color: white;
    background-color: rgb(56, 19, 98);
    border-radius: 10px;
}

.item-container img {
    height: 300px;
    width: 300px;
    object-fit: cover;
}

.item-container h1 {
    padding: 10px;
    font-size: 24px;
    text-align: center;
}

.item-container p {
    opacity: 75%;
    padding: 10px;
}

.item-container div a {
    text-decoration: none;
    color: white;
    padding: 10px;
    margin: 0px 10px;
}
