.contact-form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

.form-info {
    width: 400px;
    margin-bottom: 10px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}

label {
    text-align: start;
    margin: 0;
    padding: 10px 0px;
}

input {
    margin: 0;
    width: 400px;
    height: 40px;
    text-indent: 10px;
}

input::-webkit-input-placeholder {
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    text-indent: 10px;
    opacity: 50%;
}

input::placeholder {
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    text-indent: 10px;
    opacity: 50%;
}

textarea {
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    height: 200px;
    width: 400px;
    resize: none;
    font-size: 16px;
    padding: 5px;
}

textarea::-webkit-input-placeholder {
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    opacity: 50%;
}

textarea::placeholder {
    font-family:system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 16px;
    opacity: 50%;
}

.submit {
    margin-top: 10px;
    width: 140px;
    height: 50px;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    border: none;
    border-radius: 24px;
    background-color: rgb(56, 19, 98);
    color: white;
    font-size: 16px;
    font-weight: 500;
    cursor: pointer;
}
